.product-promo {

  &__img {
    width: 93px;
    height: 46px;
    background-color: #C4C4C4;
    margin-right: 10px;
  }

  &__content {
    max-width: 265px;
    text-align: left;
  }

  &__title {
    font-size: 12px;
    line-height: 1;
    margin-bottom: 7px;
    color: #EBEBEB;
    text-transform: uppercase;

  }

  &__text {
    color: #B1B1B1;
    font-size: 13px;
    font-weight: 400;
    font-family: var(--font-family);
  }


}
