.news-card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  border: 2px solid var(--orange400);
  padding: 26px 8px 45px 16px;
  height: 100%;
  &__img {
    margin-bottom: 11px;
  }

  &__title {
    color: var(--orange400);
    font-weight: 700;
    font-size: 25px;
    margin-bottom: 20px;
    line-height: 1.1;
  }

  &__time {
    color: var(--orange400);
    font-size: 13px;
  }

  &__list {
    margin-bottom: 23px;
    max-width: 266px;
  }

  &__item {
    font-size: 13px;
    line-height: 1.15;
  }


  &__line {
    display: inline-block;
    vertical-align: middle;
    width: 13px;
    height: 2px;
    background-color: var(--orange400);
  }

  &__btn {
    margin-top: auto;
    text-decoration: underline;
    font-size: 15px;
    padding: 13px 15px 13px 13px;
  }
}
