.category {
  border-top: 2px solid var(--orange400);
  border-bottom: 2px solid var(--orange400);
  padding: 50px 0 30px;
  margin-bottom: 50px;

  &__list {
    column-count: 4;
    column-gap: 42px;
    text-align: center;

    @include max($md) {
      column-count: auto;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 10px;
    }

    @include max($ms) {
      gap: 5px;
    }
  }

  &__item {
    break-inside: avoid;
    display: block;
    margin-bottom: 25px;
    width: 100%;
    position: relative;
    background-color: #fff;

    @include max($md) {
      margin-bottom: 0;

      &:nth-child(1) {
        grid-row: 1 / 3;
      }

      &:nth-child(5) {
        grid-column: -3;
      }

      &:nth-child(6) {
        grid-column: -2;
        grid-row: 3 / 5;
      }
    }

    @include max($ms) {
      &:nth-child(3) {
        grid-row: 1;
      }

      &:nth-child(4) {
        grid-row: 2;
      }

      &:nth-child(5) {
        grid-column: 2;
      }

      &:nth-child(6) {
        grid-column: 1 / 3;
        grid-row: 5;
      }
    }

  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    background-color: rgba(44, 44, 44, 0.63);
    background-blend-mode: color;
    height: 344px;
    color: #ECECEC;
    font-family: var(--neueMachina);
    line-height: 1;
    font-size: 20px;
    font-weight: 800;
    text-transform: uppercase;
    border: 2px solid var(--orange400);

    &--width {
      height: 203px;

      @include max($md) {
        height: 192px;
      }
    }

    &--height {
      height: 116px;

      @include max($md) {
        height: 140px;
      }

      @include max($ms) {
        height: 100%;
      }
    }

    @include max($ms) {
      font-size: 15px;
    }
  }
}
