.product-details {
  padding-bottom: 140px;
  &__inner, &__box {
    display: flex;
  }

  &__inner {
    margin-bottom: 60px;
  }

  &__slider {
    border: 1px solid var(--orange400);
    padding: 15px;
    max-width: 376px;
    width: 100%;
    margin-right: 55px;
    align-self: flex-end;
  }

  &__big {
    margin-bottom: 10px;
  }

  &__list {
    display: flex;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__info {
    max-width: 400px;
    width: 100%;
  }

  &__title {
    font-size: 35px;
    color: var(--orange400);
    margin-bottom: 8px;
    text-transform: initial;
  }

  &__code, &__available {
    font-size: 14px;
    margin-bottom: 30px;
  }

  &__price {
    color: var(--orange400);
    font-family: var(--neueMachina);
    font-weight: 800;
    font-size: 25px;
    display: inline-block;
    margin-bottom: 5px;
  }

  &__available {
    display: block;
  }

  &__brand {
    margin-bottom: 27px;
  }

  &__btn {
    display: inline-block;
    color: #EBEBEB;
    background-color: transparent;
    border: 1px solid var(--orange400);
    padding: 10px 15px;
    font-size: 14px;
  }

  &__description {
    max-width: 370px;
    margin-right: 55px;
  }

  &__heading {
    font-family: var(--neueMachina);
    font-weight: 700;
    font-size: 20px;
    margin-bottom: 10px;
  }

  &__text {
    font-size: 14px;
    p {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  .product-share {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 33px 44px;

    &__icon {
      width: 58px;
      height: 58px;
      margin: 0 auto 5px;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;
    }

    &__link {
      font-size: 14px;
      &:focus {
        box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7);
      }

      &:hover {
        color: var(--orange400);
      }

      &:active {
        color: darken($color: #fff, $amount: 15%);
      }
    }
  }


}
