.products-filter {
  font-family: var(--neueMachina);
  font-weight: 800;
  max-width: 260px;
  width: 100%;
  margin-right: 25px;
  &__title {
    margin-bottom: 35px;
    font-size: 35px;
  }

  &__text {
    font-size: 16px;

    color: var(--orange400);
    text-transform: uppercase;
    margin-bottom: 10px;
  }

  &__filters {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__filter {
    font-size: 14px;
    color: #EBEBEB;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }


}
