.form-login {

  max-width: 310px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;

  &--register {
    max-width: 430px;
  }
  &__fields {
    margin-bottom: 30px;
  }

  &__input {
    display: block;
    width: 100%;
    height: 60px;
    border: 2px solid var(--orange400);
    background-color: transparent;
    font-size: 15px;
    color: #B1B1B1;
    padding: 20px;

    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &__inner {
    display: flex;
    margin-bottom: 10px;
  }

  &__btn {
    padding: 12px 30px;
    border: none;

    &:not(:last-child) {
      margin-right: 26px;
    }

    &--password {
      padding: 12px 22px;
      font-size: 12px;
      line-height: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: transparent;
      border: 2px solid var(--orange400);
      color: var(--orange400);
    }
  }

  &__link {
    align-self: flex-end;
    text-decoration: underline;
    font-size: 13px;
    &:focus {
      box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7);
    }

    &:hover {
      color: var(--orange400);
    }

    &:active {
      color: darken($color: #fff, $amount: 15%);
    }
  }

  &__checkbox {
    margin-bottom: 45px;
  }

  &__content {
    align-items: flex-start;
    max-width: 385px;
    font-size: 15px;
    color: #B1B1B1;
    &::before {
      background-color: var(--orange400);
      border: none;
      margin-right: 0;
      width: 15px;
      height: 15px;
    }
  }
}
