.description {
  padding-bottom: 12px;

  &--about-page {
    padding-bottom: 32px;
    @include max($ms) {
      border-bottom: 2px solid var(--orange400);
      margin-bottom: 32px;
    }
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    margin-bottom: 33px;
    @include max($md) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__content {
    flex-shrink: 0;
    max-width: 475px;
    height: 100%;
    font-size: 15px;
    line-height: 1.3;
    padding: 50px 26px;
    border: 2px solid var(--orange400);
    position: relative;
    margin-right: 53px;
    &::before,
    &::after {
      content: '';
      position: absolute;
      height: 2px;
      background-color: var(--orange400);
      left: 0;
      right: 0;

    }

    &::before {
      top: 16px;
    }

    &::after {
      bottom: 16px;
    }

    p {
      margin: 0;

      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }

    span {
      color: var(--orange400);
      font-family: var(--neueMachina);
      font-weight: 800;
      font-size: 18px;
      text-transform: uppercase;
    }

    @include max($md) {
      margin-bottom: 26px;
      margin-right: 0;
    }
  }

  &__link {

    &:hover {
      text-decoration: underline;
    }
  }

  &__list {
    display: flex;
    @include max($ms) {
      flex-wrap: wrap;
      justify-content: center;
      padding: 0 10px;
    }
  }

  &__item {
    @include max($md) {
      max-width: 135px;
    }

    &:not(:last-child) {
      margin-right: 27px;
      @include max($md) {
        margin-right: 40px;
      }
      @include max($ms) {
        margin-right: auto;
        margin-bottom: 25px;
      }
    }

    &:nth-child(2n) {
      @include max($ms) {
        margin-right: 0;
      }
    }
  }

  &__icon {
    width: 120px;
    height: 110px;
    border: 2px solid var(--orange400);
    margin-bottom: 13px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 70px 64px;
    &--solutions {
      background-image: url('../img/descr-img/solutions.png');
    }

    &--distributors {
      background-image: url('../img/descr-img/distributors.png');
    }

    &--terms {
      background-image: url('../img/descr-img/terms.png');
    }

    @include max($ms) {
      width: 100%;
    }
  }

  &__text {

    p {
      margin: 0;

      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }

  &__partners {
    max-width: 830px;
    text-align: center;
    margin: 0 auto;
    font-family: var(--neueMachina);
    font-weight: 700;
    font-size: 20px;
    line-height: 1.1;
  }
}
