.news-details {
  padding-bottom: 48px;
  &__title {
    text-align: center;
    margin-bottom: 35px;

  }

  &__inner {
    border: 1px solid var(--orange400);
    padding: 41px 30px 36px 38px;
    display: flex;
    align-items: center;
  }

  &__content {
    max-width: 454px;
    width: 100%;
  }

  &__heading {
    color: var(--orange400);
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 24px;
    white-space: nowrap;
  }

  &__text {
    font-size: 13px;
    p {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  &__line {
    vertical-align: baseline;
  }

  &__link {
    text-decoration: underline;
  }
}
