.page-error {

  &__inner {
    text-align: center;
  }

  &__title {
    font-size: 60px;
    color: var(--orange400);
    margin-bottom: 10px;
  }

  &__text {
    font-size: 25px;
    text-align: center;
    max-width: 885px;
    margin: 0 auto 50px;
    p {
      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }
  }

  &__link {
    padding-top: 12px;
    padding-bottom: 12px;
    display: inline-block;
  }
}
