.basket {
  padding-top: 50px;
  padding-bottom: 140px;
  border-bottom: 2px solid var(--orange400);
  margin-bottom: 30px;
  &__inner {
    display: flex;
    justify-content: space-between;
  }

  &__content {
    max-width: 806px;
    width: 100%;
  }

  &__title {
    font-size: 40px;
    margin-bottom: 48px;

    span {
      font-size: 25px;
      color: #5F5F5F;
    }
  }

  .basket-table {
    width: 100%;
    border-collapse: collapse;
    font-family: var(--neueMachina);
    font-weight: 800;
    font-size: 16px;
    color: var(--orange400);
    text-align: center;
    margin-bottom: 20px;

    thead {
      border: 1px solid var(--orange400);
      border-top: none;
      border-bottom: none;
      background-color: var(--orange400);
      color: #2C2C2C;
      font-size: 16px;
      font-weight: 700;



      tr {

        th {
          padding: 20px;

          &:first-child {
            padding-left: 20px;
            padding-right: 0;
          }
        }
      }

    }

    tbody {
      border: 1px solid var(--orange400);

      &::before,
      &::after {
        content: "";
        color: white;
        display: block;
        height: 50px;
      }

      &::after {
        height: 70px;
      }

      tr {
        td {
          &:first-child {
            padding-right: 0;
          }
        }
      }

      td {
        padding: 20px;
      }
    }

    &__checkbox {

      &--black,
      &--orange {
        .custom-checkbox {
          &__field {
            &:checked+span::before {
              background-image: none;
            }
          }

          &__content {
            &::before {
              background-color: #C4C4C4;
              margin-right: 0;
            }
          }
        }
      }

      &--black {
        &::before {
          border: 1px solid #2C2C2C;
          background-color: #C4C4C4;
        }
      }

      &--orange {

        .custom-checkbox {
          &__field {
            &:checked+span::before {
              background-color: var(--orange400);
            }
          }

          &__content {
            &::before {
              background-color: #C4C4C4;
            }
          }
        }
      }

      &::before {
        margin-right: 0;
      }
    }


    &__img {
      width: 93px;
      height: 46px;
      background-color: #C4C4C4;
    }

  }

  .basket-count {
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--white300);

    &__btn {
      background-color: transparent;
      border: none;
      color: inherit;
      cursor: pointer;
    }

  }

  &__total {
    background-color: var(--orange400);
    padding: 28px 18px;
    align-self: flex-start;
    font-family: var(--neueMachina);
    font-weight: 800;
    color: #2C2C2C;
    text-transform: uppercase;
    position: sticky;
    top: 0;
  }

  &__sum {
    font-size: 16px;
    padding-bottom: 15px;
    margin-bottom: 15px;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: 6px;
      right: 6px;
      background-color: #2C2C2C;
      height: 2px;
      bottom: 0;
    }
  }

  &__checkout {
    font-size: 14px;

    &:hover {
      color: #C4C4C4;
    }
  }


  .form-coupon {
    display: flex;
    margin-bottom: 20px;

    &__input {
      border: 2px solid var(--orange400);
      background-color: transparent;
      height: 42px;
      padding: 15px 20px;
      margin-right: 20px;
      color: #5F5F5F;
      font-family: var(--neueMachina);
      font-weight: 800;
      font-size: 12px;
      line-height: 1;
      max-width: 167px;
      width: 100%;
    }
  }

  .contact-details {

    &__box {
      border: 2px solid var(--orange400);
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding: 20px 20px 45px 20px;
      max-width: 806px;
      margin-bottom: 42px;
    }

    &__fields {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      max-width: 431px;
      width: 100%;
      margin-bottom: 56px;
    }

    &__heading,
    .order__heading, .payment-order__heading {
      font-family: var(--neueMachina);
      font-weight: 800;
      font-size: 20px;
      color: #fff;
      text-transform: uppercase;
      margin-bottom: 28px;

      &--margin {
        margin-bottom: 15px;
      }
    }

    &__label {
      color: #B1B1B1;
      font-size: 13px;
      margin-bottom: 10px;
    }

    &__field {
      border: 2px solid var(--orange400);
      background-color: transparent;
      height: 42px;
      width: 100%;
      color: var(--white300);
      font-family: var(--neueMachina);
      font-weight: 400;
      font-size: 12px;
      line-height: 1;
      padding: 15px 20px;

      &:not(:last-child) {
        margin-bottom: 20px;
      }

      &--width {
        max-width: 343px;
      }

      &--resize {
        resize: none;
        height: 80px;
        max-width: 431px;
      }
    }

    &__checkbox, .order__checkbox {
      margin-top: 10px;
      margin-bottom: 23px;

      .custom-checkbox {

        &__field {
          &:checked+span::before {
            background-image: none;

          }
        }

        &__content {
          color: #B1B1B1;
          font-size: 13px;
          cursor: pointer;

          &::before {
            margin-right: 16px;
            border: 1px solid var(--orange400);
          }
        }
      }
    }

    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
    }
  }

  .order {

    &__table {
      max-width: 390px;
      width: 100%;
      border: 1px solid var(--orange400);
      border-collapse: collapse;
      font-weight: 700;
      font-size: 16px;
      color: #2C2C2C;
      margin-bottom: 30px;
      thead {
        background-color: var(--orange400);
        text-align: left;

        th {
          padding: 20px;
        }
      }

      tbody {
        td {
          padding: 30px 20px;
        }
      }

      tfoot {
        border: 1px solid var(--orange400);

        td {
          padding: 20px;
          background-color: var(--orange400);
        }
      }

    }

    &__name {
      font-size: 13px;
      color: #B1B1B1;
    }

    &__price {
      font-family: var(--neueMachina);
      font-weight: 800;
      font-size: 16px;
      color: var(--orange400);
      text-align: center;
      background-color: transparent;
    }

    &__text {
      color: #B1B1B1;
      font-size: 13px;
      margin-bottom: 15px;
      max-width: 420px;
    }

    &__checkbox {
      margin: 0;
    }
  }

  .payment-order {
    max-width: 500px;
    width: 100%;
    &__box {
      padding: 40px 20px;
      background-color: var(--orange400);
      margin-bottom: 62px;
      display: flex;
      flex-direction: column;
    }

    &__checkbox {
      &:not(:last-child) {
        margin-bottom: 20px;
      }

      .custom-checkbox {

        &__field {
          &:checked+span::before {
            background-color: #FEFEFE;
          }
        }

        &__content {
          cursor: pointer;
          font-size: 16px;
          color: #2C2C2C;
          font-weight: 700;
          &::before {
            margin-right: 40px;
            background-color: #2C2C2C;
            border: none;
          }
        }
      }
    }
  }

}
