.header {
  margin-bottom: 56px;

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 38px;
    margin-bottom: 19px;
    @include max($md) {
      flex-direction: column;
      padding-top: 25px;
      margin-bottom: 45px;
    }
  }

  &__logo {
    @include max($md) {
      margin-bottom: 12px;
    }
  }

  &__time {
    text-align: center;
    font-size: 13px;
  }

  &__tel {
    font-family: var(--neueMachina);
    font-size: 18px;
    font-weight: 700;
    text-align: end;
    span {
      text-decoration: underline;
      display: block;
      font-weight: 500;
    }
    @include max($md) {
      order: -1;
      margin-bottom: 43px;
    }

    @include max($ms) {
      margin-bottom: 17px;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  &__burger {
    width: 54px;
    height: 42px;
    margin-right: 4px;
    padding: 0;
    background-color: var(--orange400);
    display: flex;
    align-items: center;
    justify-content: center;
    @include max($ms) {
      width: 48px;
      height: 38px;
      margin-right: 2px;
    }
  }

  &__catalog, &__search {
    margin-right: auto;
  }

  &__search {
    background-color: var(--orange400);
    padding: 2px;
    max-width: 565px;
    width: 100%;
    @include max($md) {
      order: 3;
      flex-grow: 1;
      max-width: 100%;
      margin-top: 30px;
    }

    @include max($ms) {
      margin-top: 10px;
    }
  }


  &__favorites {
    align-self: stretch;
    padding: 12px 40px;
  }

  &__catalog, &__favorites{
    @include max($ms) {
      padding: 10px 25px;
      font-size: 15px;
    }
  }
}
