.news {
  border-bottom: 2px solid var(--orange400);
  padding-bottom: 50px;
  margin-bottom: 38px;
  &__title {
    margin-bottom: 27px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
    @include max($md) {
      grid-template-columns: repeat(auto-fill, 307px);
      gap: 30px;
      justify-content: center;
    }
  }

}
