.product-card {
  max-width: 224px;
  width: 100%;
  padding: 25px 0;
  border: 1px solid var(--orange400);

  &__img {
    margin-bottom: 13px;
  }

  &__box {
    padding: 0 11px;
    display: flex;
    flex-direction: column;
  }

  &__heading {
    margin-bottom: 24px;
  }

  &__link {
    font-size: 14px;
    font-weight: 400;
    &:focus {
      opacity: 0.67;
    }

    &:hover {
      color: var(--orange400);
    }

    &:active {
      color: darken($color: #fff, $amount: 15%);
    }
  }

  &__inner {
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: var(--neueMachina);
    font-weight: 800;
  }

  &__price {
    color: var(--orange400);
    font-size: 18px;
  }

  &__btn {
    padding: 8px 14px;
    border: 1px solid var(--orange400);
    background-color: transparent;
    color: #EBEBEB;
    cursor: pointer;
    font-size: 14px;
    line-height: 1;
    text-transform: initial;
  }
}
