.payment-methods {
  display: flex;

  @include max($md) {
    margin-bottom: 52px;
  }

  @include max($ms) {
    flex-direction: column;
  }

  &__text {
    font-family: var(--neueMachina);
    font-weight: 800;
    font-size: 20px;
    color: var(--orange400);
    margin-right: 20px;

    @include max($md) {
      margin-right: 32px;
    }

    @include max($ms) {
      margin-bottom: 15px;
      margin-right: 0;
    }
  }

  &__list {
    display: flex;
    @include max($ms) {
      flex-wrap: wrap;
      max-width: 180px;
    }
  }

  &__item {
    width: 40px;
    height: 26px;
    background-repeat: no-repeat;
    background-position: center center;

    &:not(:last-child) {
      margin-right: 7px;

      @include max($md) {
        margin-right: 11px;
      }

      @include max($ms) {
        margin-right: 7px;
      }
    }

    &--visa {
      background-image: url('../img/payment-img/visa.svg');
    }

    &--mastercard {
      background-image: url('../img/payment-img/mc.svg');
    }

    &--mir {
      background-image: url('../img/payment-img/mir.svg');
    }

    &--tinkoff {
      background-image: url('../img/payment-img/tinkoff.png');
      width: 89px;
      @include max($ms) {
        margin-top: 7px;
      }
    }
  }
}
