.car-brands {
  margin-bottom: 34px;
  padding-bottom: 40px;
  border-bottom: 2px solid var(--orange400);
  &__title {
    margin-bottom: 25px;
  }

  &__logos {
    width: 100%;
    border: 2px solid var(--orange400);
    border-collapse: collapse;
  }

  tr {
    height: 70px;
  }

  td {
    border: 2px solid var(--orange400);
    transition: background-color 0.2s;
    &:hover {
      background-color: var(--orange400);
    }

    @include max($md) {
      width: 95px;
    }
  }

  &__img {
    margin: 0 auto;
  }

  &__list {
    border: 2px solid var(--orange400);
    border-top: none;
    column-count: 5;
    column-gap: 60px;
    padding: 25px 40px 45px;
    @include max($md) {
      column-count: 3;
    }

    @include max($ms) {
      column-count: 1;
      padding: 15px;
    }
  }

  &__item {
    font-family: var(--neueMachina);
    color: var(--orange400);
    font-size: 20px;
    font-weight: 800;
    text-transform: uppercase;
    white-space: nowrap;
    &:not(:last-child) {
      margin-bottom: 7px;
    }
  }

}
