.footer {
  padding-bottom: 35px;
  padding-top: 4px;
  &__top {
    display: flex;
    margin-bottom: 39px;
    @include max($md) {
      flex-direction: column;
      align-items: center;
    }
  }


  &__logo {
    align-self: center;
    margin-right: 60px;
    @include max($md) {
      flex-direction: column;
      width: 394px;
      margin-bottom: 49px;
      img {
        width: 100%;
      }
    }

    @include max($ms) {
      width: 291px;
      margin: 0 auto 49px;
    }
  }

  &__content {
    display: flex;
    @include max($ms) {
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  &__clients {
    margin-right: 90px;
    @include max($ms) {
      margin-right: 0;
    }

  }

  &__contacts {
    margin-right: 47px;
    @include max($ms) {
      margin-right: 0;
      margin-bottom: 45px;
    }
  }

  &__clients, &__contacts, &__connect {
    @include max($md) {
      max-width: 110px;
    }
  }

  &__connect {
    @include max($ms) {
      max-width: 100%;
    }
  }

  &__text {
    margin-bottom: 12px;
    font-family: var(--neueMachina);
    color: var(--orange400);
    font-size: 20px;
    font-weight: 800;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  &__link {
    transition: color 0.2s;
    &--active {
      color: var(--orange400);
    }

    &--size {
      font-size: 14px;
    }

    &:focus {
      box-shadow: 0 0 0 2px rgba(255, 104, 56, 0.7);
    }

    &:hover {
      color: var(--orange400);
    }

    &:active {
      color: darken($color: #fff, $amount: 15%);
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    @include max($md) {
      flex-direction: column;
    }

    @include max($ms) {
      align-items: flex-start;
    }
  }

  &__copyright {
    margin-right: 135px;
    @include max($md) {
      order: 2;
      margin-right: 0;
      display: flex;
      justify-content: space-between;
      width: 100%;
    }

    @include max($ms) {
      flex-direction: column;
    }
  }

  &__privacy {
    display: block;
    color: var(--orange400);
    &:hover {
      text-decoration: underline;
    }

    @include max($ms) {
      margin-top: 3px;
    }
  }
}
